@import "./src/scss/var";

.message {
    color: #fff;

    &__inner {
        display: flex;
    }

    &__img-wrap {
        align-self: flex-end;
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }

    &__img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        object-fit: cover;
    }

    &__info {
        position: relative;
        margin-left: 15px;
        padding: 16px;
        width: 100%;
        max-width: 300px;
        font-size: 14px;
        background-color: $green-600;

        &--get {
            margin-left: 20px;
            border-radius: 5px 5px 5px 0;
        }

        &--post {
            margin-right: 20px;
            border-radius: 5px 5px 0;
        }

        &--get,
        &--post {
            &::before,
            &::after {
                content: "";
                position: absolute;
                bottom: 0;
                width: 20px;
                height: 20px;
            }
        }

        &--get::after {
            left: -20px;
            z-index: 1;
            background-color: inherit;
        }

        &--get::before {
            left: -20px;
            z-index: 2;
            border-bottom-right-radius: 60% 50%;
            background-color: $green-100;
        }

        &--post::after {
            right: -20px;
            z-index: 2;
            border-bottom-left-radius: 60% 50%;
            background-color: $green-100;
        }

        &--post::before {
            right: -20px;
            z-index: 1;
            background-color: inherit;
        }
    }

    &__author {
        margin-bottom: 5px;
        font-size: 16px;
    }

    &__text {
        margin-bottom: 10px;
        color: #f00;
    }

    &__time {
    }
}