.main-input {
    margin-bottom: 15px;
    width: 100%;
    max-width: 400px;

    &__input {
        display: block;
        padding: 15px 20px;
        width: 100%;
        outline: none;
        border: 1px solid #fff;
        border-radius: 25px;
        color: #fff;
        background-color: rgba(255, 255, 255, 0.1);

        &::placeholder {
            color: #f00;
        }

        &--error {
            border: 1px solid #f00;
        }
    }

    &__error {
        color: #f00;
        background-color: #656565;
    }
}